import React from 'react';
import { Typography, Row, Button, Col } from 'antd';
import { Ecp } from 'components/Icons';
import { AttachmentRow } from 'components/Attachment';
import classnames from 'classnames';
import { map, size } from 'lodash';
import moment from 'moment';
import InfoRow from './InfoRow';

import * as styles from '../right-preview.module.scss';

const Attachment = ({ attachment, onClick, active }) => <>
  <Row
    className={classnames(styles.attachment, { [styles.att_active]: active })}
    gutter={[0, 8]}>
    <Col span={19}>
      <AttachmentRow
        onClick={() => onClick(attachment)}
        attachment={attachment}
      />
    </Col>
    <Col span={5} className={styles.tr}>
      <Button type={'primary'} size={'small'}></Button>
    </Col>
    {size(attachment.digitalSigns) > 0 && <>
      <Col span={24}>
        <Row gutter={[0, 8]}>
          <Col span={24}>
            <Typography.Text className={styles.ecp_title} >  </Typography.Text>
          </Col>
        </Row>
        <Row gutter={[0, 8]}>
          <Col span={24}>
            {map(attachment.digitalSigns, (digitalSign, index) => {
              const { userName, serialNumber, issuerDN, subjectDN, signDate } = digitalSign.signDetails[0];
              return <div className={styles.ecp_wrapper} key={index}>
                <Ecp />
                <Row className={styles.ecp_content} gutter={[0, 8]}>
                  <InfoRow
                    title={'userName'}
                    text={userName}
                    leftColWidth={8}
                  />
                  <InfoRow
                    title={'serialNumber'}
                    text={serialNumber}
                    leftColWidth={8}
                  />
                  <InfoRow
                    title={'issuerDN'}
                    text={issuerDN}
                    leftColWidth={8}
                  />
                  <InfoRow
                    title={'subjectDN'}
                    text={subjectDN}
                    leftColWidth={8}
                  />
                  <InfoRow
                    title={''}
                    text={moment(signDate).format('HH.mm / DD.MM.YYYY')}
                    leftColWidth={8}
                  />
                </Row>
              </div>;
            })}
          </Col>
        </Row>
      </Col>
    </>}
  </Row>
</>;

export default Attachment;